import { defineComponent, PropType, toRefs } from 'vue';
import useTRouter from '@/composite/useTRouter';
import { useI18n } from 'vue-i18n';

import cn from 'classnames';

import { menus } from './config';

import styles from './entry.module.less';

export default defineComponent({
  name: 'Entry',
  props: {
    item: {
      type: Object as PropType<(typeof menus)[number]>,
      required: true,
    }
  },
  setup(props) {
    const { item } = toRefs(props);
    const { router } = useTRouter();
    const { t } = useI18n({ useScope: 'global' });

    return () => (
      <div class={cn(styles.entry)} onClick={() => router.push({
        path: item.value.path,
        replace: item.value.replace
      })}>
        <div class={cn(styles.icon)}>
          {item.value.icon}
        </div>
        <div class={cn(styles.label)}>
          {t(item.value.title)}
        </div>
      </div>
    );
  }
});

